import './new.css'
import Title from '../../UI/title'
import { newImgs, circle, dice1, dice2, hot, lol } from '../../../images/images'
import NewCard from '../../UI/newCard'
import ImgPosition from '../../UI/imgPosition'
import { useState, useEffect } from 'react'
const New = () => {
  const [width, setWidth] = useState(window.innerWidth)
  useEffect(() => {
    const setWidthHandler = () => {
      setWidth(window.innerWidth)
    }
    window.addEventListener('resize', setWidthHandler)
  }, [width])

  let convertImg = [...newImgs]
  if (width < 1600) {
    convertImg.shift()
  }
  const nImgs = convertImg.map((img, i) => {
    if (i === 0 && width < 1600) return <NewCard img={img} key={i} />
    if (i === 0) return <NewCard key={i} />
    if (i === 3) return <NewCard img={img} key={i} />

    return <NewCard img={img} key={i} colorBlack />
  })
  const title = "happened's issue"
  const introduction =
    '일에 최적화된 비디오를 단 몇 분 만에 제작해보세요. 디자이너가 아니더라도,  모든 팀에서 다듬을 수 있습니다. 브랜드의 성공을 위한 첫 걸음을  내딛어보세요. 비디오를 단 몇 분 만에 제작해보세요'
  return (
    <div className='new__container'>
      <Title title={title} introduction={introduction} name='new' button />
      <div className='new__content'>
        {nImgs}
        <ImgPosition img={lol} name='lol' />
        <ImgPosition img={circle} name='circle' />
        <ImgPosition img={hot} name='hot' />
      </div>
      <div className='new__png'>
        <ImgPosition img={dice1} name='dice1' />
        <ImgPosition img={dice2} name='dice2' />
      </div>
    </div>
  )
}
export default New
