import './newCard.css'
import { square } from '../../images/images'
const NewCard = props => {
  let content = props.img && (
    <>
      <p className='newCard_brand'>{props.img.brand} brand</p>
      <div className='newCard__brand-img'>
        <img src={props.img.img} alt='' />
      </div>
    </>
  )

  return (
    <div className={`newCard ${props.colorBlack ? 'newcard__black' : ''}`}>
      {!props.img && (
        <div className='newCard__issue'>
          <p className='newCard__issue-text'>whpn issue</p>
          <img src={square} alt='' />
          <p className='newCard__issue--arrow'>&rarr;</p>
        </div>
      )}
      {content}
    </div>
  )
}
export default NewCard
