import { star, close, videoBg } from '../../../images/images'
import Title from '../../UI/title'
import './vdHome.css'
const VdHome = () => {
  const title = 'what happened'
  const introduction =
    'How to create mobile-optimized videos in minutes. Not a designer, every team makes a lot of videos Can be trimmed. Take the first '
  return (
    <div className='vdHome__container'>
      <Title title={title} introduction={introduction} button />
      <div className='vdHome__content'>
        <div className='vdHome__content-toolbar'>
          <div className='vdHome__toolbar-whp'>
            <img src={star} alt='' />
            <p>what happened</p>
          </div>
          <div className='vdHome__toolbar-crossbar'></div>
          <img src={close} alt='' />
        </div>
        <div className='vd__content-url'>
          <p>www.whpn.com/brandstory</p>
          <p>&rarr;</p>
        </div>
        <div className='vd__content-bg'>
          <img src={videoBg} alt='' />
          <div className='vd__content-title'>
            <div className='vd__content-title2'>
              <p>happy</p>
            </div>
            <div className='vd__content-title1'>
              <span>왓헤픈의 브랜드 스토리는 이렇게 만들어지고</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default VdHome
