import './bestProduct.css'
import { bestProductImgs } from '../../../images/images'
import BestProductCard from '../../UI/bestProductCard'
import Title from '../../UI/title'
const BestProduct = () => {
  const bpimgs = bestProductImgs.map((img, i) => {
    return <BestProductCard key={i} img={img} number={i + 1} />
  })
  const title = 'best product'
  const introduction =
    'how to create mobile-optimized videos in minutes. not a designer, every team makes a lot of videos Can be trimmed.take the first'
  return (
    <div className='bestProduct__container'>
      <Title title={title} introduction={introduction} name='bestProduct' />
      <div className='bestProduct__products'>{bpimgs}</div>
    </div>
  )
}
export default BestProduct
