import './products.css'
import { productImgs } from '../../../images/images'
import ProductCard from '../../UI/productCard'
import Button from '../../UI/button'
import { useEffect, useState } from 'react'
const Products = () => {
  const [width, setWidth] = useState(window.innerWidth)
  const [hidden, setHidden] = useState(false)
  const [height, setHeight] = useState(null)
  const [jump, setJump] = useState(1)
  useEffect(() => {
    if (width > 1600) {
      setHeight(2220)
    } else {
      setHidden(false)
    }
    const setWidthHandler = () => {
      setWidth(window.innerWidth)
    }
    window.addEventListener('resize', setWidthHandler)
  }, [width])

  const hiddenProductHandler = () => {
    if (width > 1600) {
      return
    }
    if (width < 1600 && width > 1280) {
      setHeight(3000)
      setHidden(true)
    }
    if (width < 1280 && width > 960) {
      setHeight(1850 + jump * 1050)
      setJump(prevState => prevState + 1)
      setHidden(jump === 2 && true)
    }
    if (width < 960) {
      console.log(1)
      setHeight(1850 + jump * 710)
      setJump(prevState => prevState + 1)
      setHidden(jump === 2 && true)
    }
  }

  const pdImgs = productImgs.map((img, i) => {
    return <ProductCard img={img} key={i} />
  })
  return (
    <div className='propducts__container'>
      <div
        className='cards__container'
        style={{ overflow: hidden ? 'visible' : 'hidden', height: height }}
      >
        <div className='cards'>{pdImgs}</div>
        <div
          className='cards__shadow'
          style={{ display: hidden && 'none' }}
        ></div>
      </div>
      <div className='products__button'>
        <Button onclick={hiddenProductHandler} />
      </div>
    </div>
  )
}
export default Products
