import './bestProductCard.css'
import numberPng from '../../assets/png/numberPng.png'
const BestProductCard = props => {
  return (
    <div className='bestProduct__card'>
      <div className='bestProduct__card-number'>
        <img src={numberPng} alt='' />
        <span>0{props.number}</span>
      </div>
      <img className='bestProduct__card-img' src={props.img} alt='' />
      <div className='bestProduct__card-content'>
        <p>how to create mobile-optimized</p>
        <button>go</button>
      </div>
    </div>
  )
}
export default BestProductCard
